import {VNode} from 'vue';

export enum SelectType {
  None,
  /**  */
  Single,
  /** / */
  All,
  /**   */
  Only,
  /**  */
  Start,
  /**  */
  Middle,
  /**  */
  End,
}

export interface CellData {
  dayOfMonth: number;
  isFirstOfMonth: boolean;
  isLastOfMonth: boolean;
  outOfDate: boolean;
  selected: SelectType;
  tick: number;
}

export interface ExtraData {
  /** (web only) cell className */
  cellCls?: any;
  cellRender?: (date: Date) => VNode;
  /**  */
  disable?: boolean;
  /**  */
  info?: string;
  /**
   * 
   */
  selected?: boolean;
}

export interface MonthData {
  component?: VNode;
  componentRef?: {
    updateWeeks: (data?: MonthData) => any
  };
  firstDate: Date;
  height?: number;
  lastDate: Date;
  title: string;
  updateLayout?: any;
  weeks: CellData[][];
  y?: number;
}

export interface Locale {
  am: string;
  begin: string;
  begin_over: string;
  clear: string;
  confirm: string;
  dateFormat: string;
  dateTimeFormat: string;
  end: string;
  lastMonth: string;
  lastWeek: string;
  loadPrevMonth: string;
  month: string;
  monthTitle: string;
  noChoose: string;
  over: string;
  pm: string;
  selectEndTime: string;
  selectStartTime: string;
  selectTime: string;
  start: string;
  title: string;
  today: string;
  week: string[];
  year: string;
  yesterday: string;
}
