export default window.moment && window.moment.updateLocale('zh-cn', {
  months : '___________'.split('_'),
  monthsShort : '1_2_3_4_5_6_7_8_9_10_11_12'.split('_'),
  weekdays : '______'.split('_'),
  weekdaysShort : '______'.split('_'),
  weekdaysMin : '______'.split('_'),
  longDateFormat : {
    LT : 'HH:mm',
    LTS : 'HH:mm:ss',
    L : 'YYYY/MM/DD',
    LL : 'YYYYMD',
    LLL : 'YYYYMDAhmm',
    LLLL : 'YYYYMDddddAhmm',
    l : 'YYYY/M/D',
    ll : 'YYYYMD',
    lll : 'YYYYMD HH:mm',
    llll : 'YYYYMDdddd HH:mm'
  },
  meridiemParse: /|||||/,
  meridiemHour(hour, meridiem) {
    let copyHour = hour;
    if (copyHour === 12) {
      copyHour = 0;
    }
    if (meridiem === '' || meridiem === '' ||
        meridiem === '') {
      return copyHour;
    } else if (meridiem === '' || meridiem === '') {
      return copyHour + 12;
    } else {
      // ''
      return copyHour >= 11 ? copyHour : copyHour + 12;
    }
  },
  meridiem(hour, minute, isLower) {
    const hm = hour * 100 + minute;
    if (hm < 600) {
      return '';
    } else if (hm < 900) {
      return '';
    } else if (hm < 1130) {
      return '';
    } else if (hm < 1230) {
      return '';
    } else if (hm < 1800) {
      return '';
    } else {
      return '';
    }
  },
  calendar : {
    sameDay : '[]LT',
    nextDay : '[]LT',
    nextWeek : '[]ddddLT',
    lastDay : '[]LT',
    lastWeek : '[]ddddLT',
    sameElse : 'L'
  },
  dayOfMonthOrdinalParse: /\d{1,2}(||)/,
  ordinal(number, period) {
    switch (period) {
      case 'd':
      case 'D':
      case 'DDD':
        return number + '';
      case 'M':
        return number + '';
      case 'w':
      case 'W':
        return number + '';
      default:
        return number;
    }
  },
  relativeTime : {
    future : '%s',
    past : '%s',
    s : '',
    ss : '%d ',
    m : '1 ',
    mm : '%d ',
    h : '1 ',
    hh : '%d ',
    d : '1 ',
    dd : '%d ',
    M : '1 ',
    MM : '%d ',
    y : '1 ',
    yy : '%d '
  },
  week : {
    // GB/T 7408-1994ISO 8601:1988
    dow : 1, // Monday is the first day of the week.
    doy : 4  // The week that contains Jan 4th is the first week of the year.
  }
});
